                    class='col-sm-12 col-lg-2 col-xl-2 col-md-12 mb-3 div-cardView'>
                    <div class='box docattachment border'>
                        <div class="verticle-action">
                            <em class="fa fa-download text-info" @click="DownloadSingleFiles(file.Id,file.NAME)" v-if="file.IsDocCode==1"></em>
                        </div>


                        <!-- <input :value="file.Id" :folderId="file.ParentId" :data-tagIds="file.TagIds"
                            v-on:change="onChange()" :docCode="file.IsDocCode" :PinId="file.PinId" class="chkItems"
                            type="checkbox"> -->
                        <!-- <input :value="file.Id" v-on:change="onChange()" :docCode="file.IsDocCode"
                            v-bind:fileid="file.FileId" v-bind:filepath="file.FilePath" class="chkItems"
                            type="checkbox" /> -->

                        <div class="pinff pinnewtag pr-2 d-inline-block">
                            <em class="fa fa-thumb-tack" @click="MarkAsUnPin(file.Id)" v-if="file.PinId > 0"
                                aria-hidden="true"></em>
                        </div>

                        <div class='img-icons new text-center p-2 file-image doc-icon'>
                          <img  style="height: 85px;" v-if="file.DocType != '' && (file.DocExtention=='pdf' || file.DocExtention=='jpg' || file.DocExtention=='png' || file.DocExtention=='jpeg')" :class="GetFileIconClass(file.DocExtention)"
                                :alt='file.FileType' :title='file.DocType' :data-fileType='file.DocExtention' :src="GetThumbnail(file.FileThumbnailPath)" 
                                @click="openLightbox"/>
                            <em style="height: 85px;" v-if="file.DocExtention=='xls'  || file.DocExtention=='xlsx' ||  file.DocExtention=='txt' || file.DocExtention=='docx'" :class="GetFileIconClass(file.DocExtention)"
                                :alt='file.FileType' :title='file.DocType' :data-fileType='file.DocExtention'
                                @click="FilesDetail(file.Id, file.IsDocCode, file.DocPathLocation, file.DocExtention, file.ParentId, file.PERMISSION)"></em>
                            <em style="height: 85px;" v-if="file.IsDocCode==0" class='fa fa-folder pr-2 font-18 text-primary' :alt='file.FileType'
                                :title='file.DocType' @click="GetFolderList(file.Id, commonParentName)"
                                :data-fileType='file.DocExtention'></em>
                        </div>

                        <div class='row mx-0 mb-3  align-items-end'>
                            <div class='col-md-12 font-14 text-center'>
                                <span class="text-overflow-dynamic-container text-truncate" :title="file.NAME"
                                    v-if="file.IsDocCode == 0">
                                    {{ file.pin_id }}

                                    <span style='display:block;  padding-bottom:5px;' :title='file.FileName'>
                                        {{ file.NAME }}
                                    </span>
                                </span>

                                <span class="text-overflow-dynamic-container text-truncate" :title="file.NAME"
                                    v-if="file.IsDocCode == 1">
                                    <span style='display:block; padding-bottom:5px;' :title='file.FileName'>
                                        {{ file.NAME }}
                                    </span>
                                </span>
                            </div>
                            <div class='col-md-12 font-14 text-center' v-if="file.DocType != ''"><span>{{
                                convertmbtoKb(file.DocSize)
                            }} </span> |
                                <span>{{
                                     $options.filters.formatDate(file.created_at)
                                }}</span>
                            </div>
                            <div class='col-md-12 font-14 text-center' v-else>
                              <em aria-hidden="true"></em>{{ file.FileCount }} Files |
                                <em aria-hidden="true"></em>{{ convertmbtoKb(file.DocSize) }}
