    </div>       
</template>
<script>
import DataService from '../../services/DataService';
import Pager from "../../components/Common/Pager.vue"
import VueEasyLightbox from 'vue-easy-lightbox'
export default{
    components:{
        Pager,
        VueEasyLightbox
    },
    created: function(){
        this.GetFolderList();
        var decodeId = atob(window.location.search.split("=")[1].split("&")[0].replace(/[^a-z0-9\s]/gi, '').replace('3',''));
        var StorageFileId = parseInt(decodeId);
        this.BreadCrumb("MySpace",StorageFileId);
        this.$router.push({ path: "/DownloadPublicFolder", query: { FileId: btoa(this.StorageFileId), UniqueCode:window.location.search.split("UniqueCode=")[1]} });
    },
    data(){
        return{
            folderFileListing:[],
            parentname:[],
            totalRecords:null,
            pageNo:1,
            pageSize:10,
            listType:'ListView',
            visibleRef:false
        }
    },
    methods:{
        GetFolderList: function(id){
            var vm=this;
            if(id!=null){
                vm.StorageFileId=id
            }
            else{
                if(id!=undefined){
                    vm.StorageFileId=id
                }
                else{
                    var decodeId = atob(window.location.search.split("=")[1].split("&")[0].replace(/[^a-z0-9\s]/gi, '').replace('3',''));
                    vm.StorageFileId = parseInt(decodeId);
                }
               
            }
            if(parseInt(vm.StorageFileId)==parseInt(atob(window.location.search.split("=")[1].split("&")[0].replace(/[^a-z0-9\s]/gi, '').replace('3',''))))
            {
                vm.pageNo;
            }
            else{
                vm.pageNo=1;
            }
            
            var params = `folderId=${vm.StorageFileId}&pageNo=${vm.pageNo}&pageSize=${vm.pageSize}&searchFilter&sortBy&sortExp&refCode=Public`;
            DataService.GetPubLicLinkFolderList(params).then((response) => {
                vm.folderFileListing = response.data.FolderData;
                if (vm.folderFileListing.length > 0) {
                    vm.totalRecords = vm.folderFileListing[0].totalRecords;
                }
                
                setTimeout(function () {
                    vm.ResponsiveDataTable('div_forListView')
                }, 500);
                setTimeout(function () { vm.CheckBoxBootstrap() }, 100);
                // this.$router.push({ path: "/DownloadPublicFolder", query: { FileId: btoa(vm.StorageFileId)} });
                vm.BreadCrumb("MySpace",vm.StorageFileId)
            })
        },
        GetThumbnail: function(path){
            var cls=null;
            if(path!=null){
                var incStr = path.includes("wwwroot"); 
            }
            if(incStr==true){
                 cls=path.replace("/wwwroot", "");
            }
            else{
                cls=path;
            }
            return cls;
        },
        DownloadSingleFiles: function (fileId, fileName) {
            var vm = this;
            var IsVersion = 0
            var params = `fileId=${fileId}&refCode=PublicDocFile`
            DataService.DownloadPublicFileSingle(params).then((response) => {
                var fileURL = window.URL.createObjectURL(new Blob([response.data]));
                var fileLink = document.createElement('a');
                fileLink.href = fileURL;
                fileLink.setAttribute('download', fileName);
                document.body.appendChild(fileLink);
                fileLink.click();

            })
        },
        DownloadFile() {
            var vm = this;
            var FolderName=window.location.search.split("FolderName")[1];
            var params = `storageUniqueCode=${window.location.search.split("UniqueCode=")[1]}`
            DataService.GetPublicallyStorageDocs(params).then((response) => {
                var fileURL = window.URL.createObjectURL(new Blob([response.data]));
                var fileLink = document.createElement('a');
                fileLink.href = fileURL;
                fileLink.setAttribute('download', decodeURIComponent(decodeURIComponent(FolderName)) + ".zip");
                document.body.appendChild(fileLink);
                fileLink.click();
            })
        },
        pagerMethod: function (s) {
            this.pageNo=s.PageNumber;
            this.pageSize=s.PageSize;
            if (parseInt(this.pageNo) > 0) {
              this.GetFolderList(this.StorageFileId);
            }
            else {
                this.pageNo = 1
            }
        },
         openLightbox: function () {
            var vm = this;
            vm.visibleRef = true
        },
        onHide: function () {
            var vm = this;
            vm.visibleRef = false
        },
        pageSizecommon: function (e) {
            var vm = this;
            vm.pageSize = e.target.value
            this.GetFolderList();
        },
        GetFileIconClass: function (ext) {
            ext=ext.toLowerCase();
            var fileclass = "";
            if (ext == 'png' || ext == "jpeg" || ext == "jpg" || ext == "JPG" || ext == "jfif") {
                fileclass = "fa fa-file-image-o";
            }
            if (ext == "pdf") {
                fileclass = "fa fa-file-pdf-o"
            }
            if (ext == "xlsx") {
                fileclass = "fa fa-file-excel-o"
            }
            if (ext == "txt") {
                fileclass = "fa fa-file-text-o"
            }
            if (ext == "xls") {
                fileclass = "fa fa-file-excel-o"
            }
            if (ext == "docx") {
                fileclass = "fa fa-file-word-o"
            }
            return fileclass
        },
        convertmbtoKb: function (val) {
            var convertKb = 0;
            if (val == 0) {
                convertKb = '0 Bytes'
            } else
                if (val < 1) {
                    convertKb = val.toFixed(2) + ' KB'
                }
                else {
                    convertKb = val.toFixed(2) + ' MB'
                }
            return convertKb;
        },
        BreadCrumb(name, id) {
            var vm = this;
            if (id != undefined) {
                vm.folderId = id;
            }
            vm.parentname = []
            vm.commonParentName = name;
            if (vm.folderId == undefined || vm.folderId == "") {
                vm.folderId = 0;
            }
            DataService.GetPublicLinkFolderBreadCrumb(vm.folderId).then((response) => {

                var numbersString = response.data;
                var numbersArray = numbersString.split('>');
                numbersArray.forEach((value, index) => {
                    vm.parentname.push(value.split("~"))
                })
                vm.closepopup;
                DataService.GetParentFolderBreadCrumb(vm.folderId).then((response) => {
                    vm.breadcrumbForManagePopup = response.data;
                })
            });

        },
        SpecificFolder: function (id, name) {
            var vm = this;
            var scondition = "";
            vm.GetFolderList(id)
        },
    }
}
</script>
